import React, { useEffect, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import cookies from 'js-cookie';

import cl from "./Navbar.module.scss";
import './Navbar.scss'


import SidebarMenu from '../SidebarMenu/SidebarMenu';

import { ThemeContext } from '../../context/themes';
import { themes } from '../../context/themes';
import { languages } from '../../languages/languages';

import { NavLink, Link } from 'react-router-dom';

import { useSelector } from 'react-redux';

import ThemeSvg from './svg-icons/ThemeSvg';
import LangSvg from './svg-icons/LangSvg';
import Dropdown from '../UI/dropdown/Dropdown';


const Logo = function () {
    return (
        <div className={`${cl.logo_wrapper} logo_wrapper`}>
            <Link data-title=' ' to="/">
                LOGO
            </Link>
        </div>
    )
}

const NavLinks = function (props) {

    const { t } = props

    const navlinks = [
        { 'name': `${t('main_content.menu.main', "main")}`, 'link': '/' },
        { 'name': `${t('main_content.menu.about_me', "about me")}`, 'link': 'about-me' },
        { 'name': `${t('main_content.menu.contact_me', "contact me")}`, 'link': 'contact-me' }
    ]

    return (
        <nav className={cl.navigation}>
            {/* // nav_links_wrapper */}

            {navlinks.map((l, index) =>
                <NavLink
                    key={index}
                    to={l.link}

                    data-title={`  ${l.name}`}
                    className={
                        (navData) => navData.isActive ? `${cl.nav_link} ${cl.active} nav_link active` : `${cl.nav_link} cl.nav_link`
                    }
                >
                    {l.name}
                </NavLink>
            )}

        </nav>
    )
}


const SelectLanguage = () => {

    const { t, i18n } = useTranslation()

    const currentLanguageCode = cookies.get('i18next') || 'en'

    const currentLanguage = languages.find((l) =>
        l.code === currentLanguageCode
    )

    const languages_object = {}
    for (let c = 0; c < languages.length; c++) {
        languages_object[languages[c]['code']] = languages[c]['name']
    }
    Object.freeze(languages_object)

    const changeLanguage = (language) => {
        i18n.changeLanguage(language)
    }


    useEffect(() => {
        document.body.dir = currentLanguage.dir || 'ltr'
    }, [currentLanguage, t])

    return (
        <Dropdown
            values={
                languages_object
            }
            defaultValue={currentLanguageCode}
            callback={changeLanguage}

            headerElement={<span className={`${cl.select_icon_wrapper} ${cl.change_language_svg_wrapper}`}><LangSvg /></span>}
        />
    )
}

const SelectTheme = ({ changeTheme }) => {


    const theme = useContext(ThemeContext);

    let themes_object = {}
    for (let t = 0; t < themes.length; t++) {
        themes_object[themes[t]] = themes[t]
    }
    Object.freeze(themes_object)

    const [currentTheme, setCurrentTheme] = useState(theme)

    const chTheme = (th) => {

        changeTheme(th)
        setCurrentTheme(th)
    }

    return (
        <Dropdown
            values={themes_object}
            defaultValue={currentTheme}
            callback={chTheme}

            headerElement={
                <span className={`${cl.select_icon_wrapper} ${cl.change_theme_svg_wrapper}`}>
                    <ThemeSvg />
                </span>
            }
        />
    )
}

const Burger = (props) => {
    const { sidemenuIsOpened, setSidemenuIsOpened } = props

    return (
        <b
            onClick={() => setSidemenuIsOpened(!sidemenuIsOpened)}
            className={`${cl.burger_wrapper_main} burger`}
        >
            <span
                className={cl.burger_wrapper}>
                <span className={cl.burger}>
                    <span className={cl.burger_line} />
                    <span className={cl.burger_line} />
                    <span className={cl.burger_line} />
                </span>
            </span>
        </b>
    )
}


function Navbar(props) {

    const { changeTheme } = props

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const { t } = useTranslation()

    const [sidemenuIsOpened, setSidemenuIsOpened] = useState(true)

    return (
        <React.Fragment>
            <header
                id="NAV"
                className={`${cl.nav_main_wrapper} nav_active nav`}
            >
                <div className={cl.nav_content}>

                    <Logo />

                    <NavLinks
                        t={t}
                    />

                    <section className={cl.nav_content_right}>

                        {!isUserAuthenticated &&
                            <div className={`${cl.auth_links}`}>
                                {[
                                    {
                                        'name': `${t('main_content.menu.top_content.login', "login")}`,
                                        'link': "/users/login"
                                    },
                                    {
                                        'name': `${t('main_content.menu.top_content.register', "register")}`,
                                        'link': "/users/register"
                                    }
                                ].map((l, index) =>
                                    <NavLink
                                        key={index}
                                        to={l.link}


                                        data-title={`  ${l.name}`}

                                        className={
                                            (navData) => navData.isActive ? `${cl.auth_link} ${cl.active}` : `${cl.auth_link}`
                                        }
                                    >
                                        {l.name}
                                    </NavLink>
                                )
                                }
                            </div>
                        }

                        <section className={cl.nav_content_right__end}>
                            <SelectLanguage />
                            <SelectTheme changeTheme={changeTheme} />
                            <Burger sidemenuIsOpened={sidemenuIsOpened} setSidemenuIsOpened={setSidemenuIsOpened} />
                        </section>

                    </section>

                </div>
            </header>

            <SidebarMenu sidemenuIsOpened={sidemenuIsOpened} setSidemenuIsOpened={setSidemenuIsOpened} />
        </React.Fragment >
    )
}

export default Navbar;