const MatrixRain = (props) => {
    const canvasRef = useRef(null)

    let letters = ''
    letters += ''
    letters += 'QWERTYUIOPASDFGHJKLZXCVBNM'
    letters += ''
    letters += '0101010101010101010101010101'
    // letters += '!@#$%^&*()_+=-}{":\'\\/'

    const letters_list = letters.split('')

    const SPEED = 25
    const FONT_SIZE = 11

    useEffect(() => {
        const canvas = canvasRef.current
        const ctx = canvas.getContext('2d')

        canvas.width = window.innerWidth
        canvas.height = window.innerHeight

        let drops = [];
        function setDrops() {
            drops = []
            // Setting up the columns
            const columns = canvas.width / FONT_SIZE;

            // Setting up the drops
            for (let i = 0; i < columns; i++) {
                drops[i] = 1;
            }
        }

        function fixAdaptivity() {
            if (window.innerWidth !== canvas.width) {
                canvas.width = window.innerWidth
                setDrops()
            }
            if (window.innerHeight !== canvas.height) {
                canvas.height = window.innerHeight
                setDrops()
            }
        }

        if (canvas && canvas.getContext) {

            setDrops()

            function draw() {
                fixAdaptivity()

                ctx.fillStyle = 'rgba(0, 0, 0, 0.05)';
                ctx.fillRect(0, 0, canvas.width, canvas.height);

                for (let i = 0; i < drops.length; i++) {
                    let text = letters_list[Math.floor(Math.random() * letters_list.length)];

                    ctx.fillStyle = '#0F0';
                    ctx.font = `normal ${FONT_SIZE}px Arial`;

                    let letter_x_pos = i * FONT_SIZE
                    let letter_y_pos = drops[i] * FONT_SIZE

                    ctx.fillText(text, letter_x_pos, letter_y_pos);

                    drops[i]++;
                    if (drops[i] * FONT_SIZE > canvas.height && Math.random() > .95) {
                        drops[i] = 0;
                    }
                }
            }


            let drawInterval = setInterval(draw, SPEED)

            return () => {
                clearInterval(drawInterval)
            }

        }

    }, [canvasRef.current])

    return (
        <div className={cl.canvasWrapper}>
            <canvas
                ref={canvasRef}
                {...props}
                className={cl.canvas}
            >
                Your Browser does not support it
            </canvas>
        </div>
    )
}


const ScrollDownBtn = function () {

    let windowHeight = document.documentElement.clientHeight

    function scrollDown() {
        window.scrollTo({ left: 0, top: windowHeight, behavior: 'smooth' })
    }

    return (
        <button
            onClick={scrollDown}
            className={cl.scrollDownBtn}
        >
            SCROLL DOWN
        </button>
    )
}


const Skill = function (props) {
    const { skill } = props

    const [isOpened, setIsOpened] = useState(false)



    const [ashowHint, setAshowHint] = useState(
        (skill.name).toLowerCase() === 'Javascript'.toLowerCase()
    )

    function open_hint() {
        setIsOpened(!isOpened)

        if (ashowHint) {
            setAshowHint(null)
        }
    }

    return (
        <article
            onClick={open_hint}
            className={`${cl.skill_wrapper} ${isOpened && cl.skill_description_is_opened}`}
        >
            <div className={cl.skill_name_wrapper}>
                <span
                    data-title={!isOpened ? 'Open hint' : null}
                    className={`${cl.skill_name} ${ashowHint && 'show_data_title'}`}
                >
                    {skill.name}
                </span>
                <span
                    data-title={'close'}
                    className={cl.close_btn}
                >
                    X
                </span>
            </div>
            <div className={`${cl.skill_description_wrapper}`}>
                <div className={cl.skill_description_text}>
                    {skill.description.trim()}
                </div>
            </div>
        </article>
    )
}



