import { FC } from 'react'
import ReactHtmlParser from "react-html-parser";
import DOMPurify from "dompurify";
import cl from './TextMessage.module.scss';
import { format_message_string } from '../../../../utils/format_message_string';


interface Idata {
    message: string;
    username: string;
    time: string;
    isLoading?: boolean;
}

interface TextMessageProps {
    data: Idata;
    isOutgoing: boolean;
}

interface TextMessageInfoProps {
    isOutgoing: boolean,
    time: string,
    isMessageLoading?: boolean
}

const MessageInfo: FC<TextMessageInfoProps> = (props) => {

    const { isOutgoing, isMessageLoading, time } = props

    // const isMessageLoading = false

    const seen = true

    return (
        <div className={cl.message_info}>
            <span className={cl.time}>
                {time}
            </span>

            {(() => {
                if (isOutgoing) {
                    if (isMessageLoading) {
                        return (
                            <span>
                                Loading...
                            </span>
                        )
                    } else {
                        if (seen) {

                            return (
                                <span>
                                    +
                                </span>
                            )
                        } else { // not seen
                            console.assert(!isOutgoing)
                            console.assert(!seen)
                            return (
                                <span>
                                    -
                                </span>
                            )
                        }
                    }
                }
            })()}

        </div>
    )
}


const TextMessage: FC<TextMessageProps> = (props) => {
    const { data, isOutgoing } = props

    const content = ReactHtmlParser(
        DOMPurify.sanitize(
            format_message_string(data.message)
        )
    )

    return (
        <div className={`${cl.main_wrapper} ${isOutgoing ? cl.outgoing : cl.incoming}`}>
            <div className={`${cl.content_wrapper}`}>
                <div className={cl.content}>
                    {!isOutgoing &&
                        <span className={cl.username}>
                            {data.username}
                        </span>
                    }
                    <div className={cl.message_text}>

                        {content}

                        <MessageInfo
                            isOutgoing={isOutgoing}
                            isMessageLoading={data.isLoading}
                            time={data.time}
                        />

                    </div>
                </div>
            </div>
        </div>
    )
}

export default TextMessage;